	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/five3d-a-different-approach-to-3d-flash/index.md absPath of file >>> MarkdownRemark","html":"<p><a href=\"http://five3d.mathieu-badimon.com/\" title=\"Open link in a new window\"><img src=\"/content/images/2008/05/five3d.jpg\" title=\"five3d\"></a></p>\n<p>FIVe3D is unlike other 3D engines ( such as Papervision or Alternitva3D ) in it's approach.  We're not talking shape primatives here, we're talking extending the basic flash elements ( Sprites, Text, MovieClips ) to include Z-properties.  Looking at the demos, it's not what you'd expect from 'Flash 3D', because we're expecting another Papervision clone.  But no, the approach that Mathieu has taken when developing this is much different - and actually makes much more sense for a lot of situations.</p>\n<p>Plus, it's fun.  I'm a sucker for fun demos.</p>\n<p><a href=\"http://five3d.mathieu-badimon.com/\" title=\"Open link in a new window\">Check it out here.</a></p>","frontmatter":{"title":"FIVe3D - A different approach to 3D Flash","date":"May 15, 2008","tags":["external-links","flash"],"url":null,"image":null,"description":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/inline-javascript-and-the-defer-attribute/","tags":["javascript","embedding-flash"],"title":"Inline Javascript and the defer attribute","date":"2008-05-11T08:30:32.000Z","description":null,"image":null}},"next":{"frontmatter":{"path":"/blog/immersive-media-360-flash-video/","tags":["external-links","flash"],"title":"Immersive Media : 360° Flash Video","date":"2008-05-27T14:56:30.000Z","description":null,"image":null}}}}

